import { router } from '../router'

const API_URL          = 'http://localhost:4000/api'
const REGISTRATION_URL = `${API_URL}/registrations/`
const SESSION_URL      = `${API_URL}/sessions/`
const CURRENT_USER_URL = `${API_URL}/current_user/`

export default {

  user: {
    authenticated: localStorage.getItem("id_token") ? true : false
  },

  login(context, creds, redirect) {
    context.$http.post(SESSION_URL, creds).then(data => {
      localStorage.setItem('id_token', data.body.jwt)

      this.user.authenticated = true

      if(redirect) {
        router.push({path: redirect})
      }
    }, error => {
      context.error = error.body
    })
  },

  currentUser(context) {
    context.$http.get(CURRENT_USER_URL, {headers: this.getAuthHeader()})
      .then(data => {
          context.user = data.body.user
        }, error => {
          console.log(error)
        })
  },

  signup(context, creds, redirect) {
    context.$http.post(REGISTRATION_URL, creds).then(data => {
      localStorage.setItem('id_token', data.body.jwt)

      this.user.authenticated = true

      if(redirect) {
        router.push({path: redirect})
      }
    }, errors => {
      context.errors = errors.body
    })
  },

  logout(context, options) {
    context.$http.delete(SESSION_URL, options).then(data => {
      localStorage.removeItem('id_token')
      this.user.authenticated = false
      router.push({path: "/login"})
    }, error => {
      console.log(error.message)
    })
  },

  checkAuth() {
    const jwt = localStorage.getItem('id_token')
    this.user.authenticated = jwt ? true : false
  },

  getAuthHeader() {
    return {
      'Authorization': localStorage.getItem('id_token')
    }
  }
}



