<template>
  <div>
    <h2>Sign Up</h2>
    <p>Sign up for a free account!</p>
    <div>
      <input
        type="text"
        placeholder="Enter your username"
        v-model="credentials.username"
      >
      <div v-if="usernameError">
        <p>{{ usernameError }}</p>
      </div>
    </div>
    <div>
      <input
        type="password"
        placeholder="Enter your password"
        v-model="credentials.password"
      >
      <div v-if="passwordError">
        <p>{{ passwordError }}</p>
      </div>
    </div>
    <button @click="submit()">Register</button>
  </div>
</template>

<script>

  import auth from '../auth'
  import { errorFor } from '../helpers'

  export default {

    data() {
      return {
        credentials: {
          username: '',
          password: ''
        },
        errors: []
      }
    },

    computed: {
      usernameError() {
        return errorFor(this.errors, "username")
      },
      passwordError() {
        return errorFor(this.errors, "password")
      }
    },

    methods: {
      submit() {
        const credentials = {
          username: this.credentials.username,
          password: this.credentials.password
        }

        auth.signup(this, {user: credentials}, '/')
      }
    }
  }
</script>



